body{
  background-image:url('./winter.jpg');
  background-position: center center;
  background-size: cover;
  min-height: 100vh;
  background-repeat:no-repeat;
}

.main{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  color: rgb(0, 0, 0);
}

.big{
  font-size: 5rem;
  margin-bottom: 0;
}

.header{
  font-size: 3rem;
  font-family: 'Gwendolyn';
  font-weight: 900;
  margin-bottom: 0;
}

.container{
  display: flex;
  font-family:'Times New Roman', Times, serif;
}

.containers{
  margin:0 2vw;
  text-align: center;
}

@media screen and (min-width<800) {
  .container{
    display: flex;
    flex-direction: column;
  }
}